* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
}

body {
  min-height: 100vh;
  display: grid;
  justify-items: center;
}

html {
  scroll-behavior: smooth;
}

img,
svg {
  max-width: 100%;
}

a,
a:hover,
a:visited,
a:focus {
  text-decoration: none;
}

.module {
  width: 100vw;
  display: grid;
  align-items: center;
  align-content: start;
  gap: 2rem;
  margin-top: 96px;
}
@media (min-width: 768px) {
  .module {
    grid-template-columns: 1fr 1fr;
    gap: 50px;
  }
}
@media (min-width: 1440px) {
  .module {
    width: 1440px;
    gap: 95px;
  }
}
.module__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 1.25rem;
  min-height: 220px;
  padding-inline: 1rem;
  padding-block: 0.5rem;
}
@media (min-width: 576px) and (max-width: 768px) {
  .module__content {
    padding-inline: 2rem;
  }
}
@media (min-width: 768px) {
  .module__content {
    justify-self: left;
    max-width: 380px;
    min-height: 250px;
    padding-block: 0;
  }
}
.module__image {
  width: 100%;
  height: auto;
  padding-inline: 1rem;
  padding-top: 1rem;
  object-fit: contain;
}
@media (min-width: 576px) and (max-width: 768px) {
  .module__image {
    padding-inline: 2rem;
  }
}
@media (min-width: 768px) {
  .module__image {
    max-width: 505px;
    height: 300px;
    justify-self: right;
    padding-top: 0;
    padding-right: 0;
  }
}
.module__title, .alert__title {
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-size: 2.625rem;
  font-weight: 700;
  color: #121212;
}
.module__text, .alert__text {
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-size: 1rem;
  font-weight: 300;
  color: #121212;
  text-align: justify;
  line-height: 150%;
}

.loading {
  display: grid;
  place-items: center;
  width: 80px;
  height: 80px;
}

.loading::after {
  content: "";
  position: absolute;
  left: calc(50% - 32px);
  width: 64px;
  height: 64px;
  border-radius: 50%;
  border: 6px solid #000;
  border-color: #000 #000 #000 transparent;
  animation: loading-spin 1.2s linear infinite;
}

@keyframes loading-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.modal {
  display: none;
  place-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: rgba(18, 18, 18, 0.5);
}

.button {
  cursor: pointer;
  width: 128px;
  height: 48px;
  padding: 16px 40px 16px 40px;
  background-color: #e4c1ae;
  border: none;
  border-radius: 32px;
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  font-weight: 700;
  color: #fff;
  text-align: center;
}

.alert__content {
  justify-self: center;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  width: 100%;
  max-width: 920px;
  min-height: 210px;
  max-height: 100vh;
  padding-block: 0.25rem;
  padding-inline: 1rem;
  background-color: #fff;
}
@media (min-width: 576px) {
  .alert__content {
    row-gap: 24px;
  }
}
@media (min-width: 1440px) {
  .alert__content {
    width: 920px;
    height: fit-content;
    max-height: 90vh;
    padding: 56px 46px 56px 46px;
  }
}
.alert__table-wrapper {
  grid-column: span 2;
  place-self: center;
  width: 100%;
  overflow: auto;
  max-height: 500px;
}
.alert__table {
  min-width: 880px;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-size: 1rem;
  color: #121212;
  border-collapse: collapse;
}
.alert__table td,
.alert__table th {
  padding-left: 0.25rem;
  border: #000 1px solid;
}
@media (min-width: 992px) {
  .alert__table {
    min-width: auto;
  }
}
@media (min-width: 1440px) {
  .alert__table {
    max-height: 500px;
  }
}
.alert__title {
  font-size: 2rem;
}
@media (min-width: 576px) {
  .alert__title {
    font-size: 2.625rem;
  }
}
.alert__text {
  grid-column: span 2;
  justify-self: center;
  padding-block: 12px;
  text-align: center;
}
@media (min-width: 576px) {
  .alert__text {
    grid-column: auto;
    justify-self: left;
    text-align: justify;
  }
}
.alert__close-button {
  cursor: pointer;
  position: relative;
  justify-self: right;
  width: 12px;
  height: 12px;
  background: none;
  border: none;
}
.alert__close-button::after {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  width: 2px;
  height: 12px;
  transform: rotate(45deg);
  background-color: #000;
}
.alert__close-button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  width: 2px;
  height: 12px;
  transform: rotate(-45deg);
  background-color: #000;
}
@media (min-width: 1440px) {
  .alert__close-button {
    margin-top: -10px;
  }
}
.alert__text--semibold {
  font-weight: 600;
}

.button--reset {
  display: none;
  justify-self: center;
  grid-column: span 2;
}
@media (min-width: 576px) {
  .button--reset {
    grid-column: auto;
    justify-self: right;
  }
}