* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	list-style-type: none;
}

body {
	min-height: 100vh;
	display: grid;
	justify-items: center;
}

html {
	scroll-behavior: smooth;
}

img,
svg {
	max-width: 100%;
}

a,
a:hover,
a:visited,
a:focus {
	text-decoration: none;
}
